.block_dialog {
    display: flex;
    justify-content: space-between;
    padding: 20px 10px;
    position: relative;
}

.block_dialog:after {
    content: '';
    position: absolute;
    right: 20px;
    top: 0;
    width: 65%;
    height: 2px;
    background-color: rgb(26, 7, 80);
}

.block_dialog:before {
    content: '';
    position: absolute;
    right: 0;
    top: 20px;
    width: 2px;
    height: 35%;
    background-color: rgb(26, 7, 80);
}

.dialogs_inner {
    margin-top: 25px;
    max-width: 850px;
    width: 100%;
}

.dialogs_items {
    max-width: 150px;
    width: 100%;
}

.messages {
    width: 100%;
}

.message {
    display: flex;
    align-items: flex-start;
}

.message + .message {
    margin-top: 25px;
}
.dialog {
    font-size: 18px;
    font-weight: 600;
}

.dialog + .dialog {
    margin-top: 15px;
}

.title {
    font-size: 25px;
    font-weight: 900;
    color: #04236e;
}

.avatar {
    object-fit: cover;
    border-radius: 5px;
}

.text {
    background-color: #000;
    opacity: 0.85;
    color: #fff;
    font-size: 14px;
    margin-left: 25px;
    padding: 5px 7px;
    border-bottom-right-radius: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    position: relative;
}

.text:after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-top-width: 15px;
    border-bottom-width: 0px;
    border-right: 15px solid black;
    bottom: 0px;
    left: -15px;
}